body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: #9acece;
  margin: 10px 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.editorWrap {
  width: 600px;
  margin: 18px auto;
}

div.toolbar {
  width: 99%;
}

.previewWrap .toolbar {
  left: -1px;
  width: 100%;
  padding-right: 17px;
}

.toolbar {
  
  position: relative;
  background-color: #67b3b3;
  padding: 4px 4px 3px 3px;
  border: 1px solid #1d2f2f;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
  font-family: Russo One;
  font-size: 15px;
}

.toolbar i {
  width: 25px;
  color: black;
  margin-left: 5px;
}

.fa-free-code-camp {
  margin-right: 3px;
}

.fa {
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.editorWrap textarea, .previewWrap {
  width: 99%;
  min-height: 200px;
  margin-bottom: -5px;
  resize: vertical;
  outline: none;
  padding-left: 5px;
  padding-top: 5px;
  font-size: 12px;

  background-color: #c0d8d8;
  border: 1px solid #1d2f2f;
  box-shadow: 1px 1px 10px 2px #3a5f5f;
  border-top: none;
}

.converter {
  width: 100px;
  text-align: center;
  font-size: 35px;
  margin: auto;
}

.previewWrap {
  width: 800px;
  margin: 20px auto;
  min-height: 200px;
  overflow-wrap: break-word;
  padding: 0px 20px 0px 0px;
}

.previewWrap #preview {
  margin-left: 5px;
  margin-top: -10px;
  width: 100%;
}